import { axiosInstance } from "../../../axiosInstance";
import { useMutation, useQuery, useQueryClient } from "@tanstack/react-query";
import { queryKeys } from "../../../react-query/constants";
import { useNavigate } from "react-router-dom";
import { useContext } from "react";
import { AuthContext } from "../../../context";
import { getStoredUser, setStoredUser } from "../../../storage";
import { errorAlert, isAuthenticated, successAlert } from "../../../utils";

const SERVER_ERROR = "There was an error contacting the server.";

const userLogin = async (formData) => {
  const data = await axiosInstance({
    url: "/auth",
    method: "POST",
    data: formData,
    headers: {
      "Content-Type": "application/json",
    },
  });
  return data?.data;
};

const userProfile = async () => {
  const data = await axiosInstance({
    url: "/auth",
    method: "GET",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${getStoredUser()}`,
    },
  });
  return data?.data?.data;
};

export function useLogin() {
  const authCtx = useContext(AuthContext);
  const navigate = useNavigate();
  const queryClient = useQueryClient();
  const { mutate, isSuccess, reset, isError, error } = useMutation({
    mutationFn: (formData) => userLogin(formData),
    onSuccess: (data) => {
      successAlert("Login Successful");
      setStoredUser(data);
      authCtx.authenticate(data);
      queryClient.invalidateQueries([queryKeys.user]);
      navigate("/");
    },
  });
  return { mutate, isSuccess, reset, isError, error };
}

export function useAuthenticatedUser() {
  const authCtx = useContext(AuthContext);
  const fallback = undefined;
  const { data = fallback } = useQuery({
    enabled: isAuthenticated(),
    queryKey: [queryKeys.user],
    queryFn: () => userProfile(),
    onSuccess: (data) => {
      authCtx.updateUser(data);
    },
    onError: (error) => {
      authCtx.logout();
      const err = error?.response?.data?.error
        ? error?.response?.data?.error
        : SERVER_ERROR;

      errorAlert(err);
    },
  });
  return data;
}
