import { axiosInstance } from "../../../axiosInstance";
import { useMutation, useQuery } from "@tanstack/react-query";
import { queryKeys } from "../../../react-query/constants";

import { errorAlert } from "../../../utils";
import { getStoredUser } from "../../../storage";

const SERVER_ERROR = "There was an error contacting the server.";

const getExamSections = async (id) => {
  const data = await axiosInstance({
    url: `/exam/sections/${id}`,
    method: "GET",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${getStoredUser()}`,
    },
  });
  return data?.data?.data;
};

const getSectionQuest = async (id) => {
  const data = await axiosInstance({
    url: `/exam/questions/${id}`,
    method: "GET",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${getStoredUser()}`,
    },
  });
  return data?.data?.data;
};

const getAnsweredQuest = async (id) => {
  const data = await axiosInstance({
    url: `/question/answered/${id}`,
    method: "GET",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${getStoredUser()}`,
    },
  });
  return data?.data?.data;
};

const answerQuest = async (formData) => {
  const data = await axiosInstance({
    url: `/exam/questions/${formData["questionId"]}/candidates/${formData["candidateId"]}/exams/${formData["examId"]}/submissions/${formData["submitId"]}`,
    method: "PUT",
    data: {
      Score: formData["Score"],
      SelectedChoice: formData["SelectedChoice"],
      SectionTitle: formData["SectionTitle"],
      ExamScheduleIdId: formData["ExamScheduleIdId"],
    },
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${getStoredUser()}`,
    },
  });
  return data?.data?.data;
};

export function useExamSection(id) {
  const fallback = [];
  const { data = fallback, isLoading } = useQuery({
    queryKey: [queryKeys.section],
    queryFn: () => getExamSections(id),

    onError: (error) => {
      const err = error?.response?.data?.error
        ? error?.response?.data?.error
        : SERVER_ERROR;

      errorAlert(err);
    },
  });
  return { data, isLoading };
}

export function useSectionQuest(id) {
  const fallback = [];
  const { data = fallback, isLoading } = useQuery({
    enabled: !!id,
    queryKey: [queryKeys.question, id],
    queryFn: () => getSectionQuest(id),

    onError: (error) => {
      const err = error?.response?.data?.error
        ? error?.response?.data?.error
        : SERVER_ERROR;

      errorAlert(err);
    },
  });
  return { data, isLoading };
}

export function useAnswerQuest() {
  const { mutate, isSuccess, reset } = useMutation({
    mutationFn: (formData) => answerQuest(formData),

    onError: (error) => {
      const err = error?.response?.data?.error
        ? error?.response?.data?.error
        : SERVER_ERROR;

      errorAlert(err);
    },
  });
  return { mutate, isSuccess, reset };
}

export function useAnsweredQuest(id) {
  const fallback = [];
  const {
    data = fallback,
    refetch,
    isLoading,
  } = useQuery({
    enabled: !!id,
    queryKey: [queryKeys.answered, id],
    queryFn: () => getAnsweredQuest(id),

    onError: (error) => {
      const err = error?.response?.data?.error
        ? error?.response?.data?.error
        : SERVER_ERROR;

      errorAlert(err);
    },
  });
  if (isLoading) return [];
  return { data, refetch, isLoading };
}
