import { useContext } from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import AuthContextProvider, { AuthContext } from "./context";
import { Dashboard, ExamScreen, HomeScreen } from "./modules";

function App() {
  const Stack = () => {
    return (
      <Routes>
        <Route path="/" exact element={<HomeScreen />} />
      </Routes>
    );
  };

  const AuthStack = () => {
    return (
      <Routes>
        <Route path="/" exact element={<Dashboard />} />
        <Route path="/exam/:id" exact element={<ExamScreen />} />
      </Routes>
    );
  };

  const AuthGate = () => {
    const { user } = useContext(AuthContext);
    if (user) {
      return <AuthStack />;
    }
    return <Stack />;
  };

  return (
    <Router>
      <AuthContextProvider>
        <AuthGate />
      </AuthContextProvider>
    </Router>
  );
}

export default App;
