import { useMutation, useQueryClient } from "@tanstack/react-query";
import { toast } from "react-toastify";
import { axiosInstance } from "../../../axiosInstance";
import { queryKeys } from "../../../react-query/constants";
import { getStoredUser } from "../../../storage";
import { toastOptions } from "../../../utils";

const SERVER_ERROR = "There was an error contacting the server.";

const updateUser = async (formData) => {
  const data = await axiosInstance({
    url: `/auth/`,
    method: "PUT",
    data: formData,
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${getStoredUser()}`,
    },
  });
  return data;
};

export function useUpdateUser() {
  const queryClient = useQueryClient();
  const { mutate, isSuccess, reset } = useMutation({
    mutationFn: (formData) => updateUser(formData),
    onSuccess: (data) => {
      toast.success("User Data Updated Successfully", toastOptions);
      queryClient.invalidateQueries([queryKeys.user]);
    },
    onError: (error) => {
      const err = error?.response?.data?.responseMessage
        ? error?.response?.data?.responseMessage
        : SERVER_ERROR;
      toast.error(err, toastOptions);
      console.log(error);
    },
  });
  return { mutate, isSuccess, reset };
}
