import React, { useContext, useEffect } from "react";
import styles from "./styles.module.css";
import { FiEdit } from "react-icons/fi";
import { AiOutlineClose } from "react-icons/ai";
import { ImProfile } from "react-icons/im";
import Input from "../Input";
import logo from "../../assets/logo.png";
import { AuthContext } from "../../context";
import { useUpdateUser } from "./hooks";
import { useIsMutating } from "@tanstack/react-query";

const Navigation = () => {
  const { user } = useContext(AuthContext);

  const [formData, setFormData] = React.useState({});

  const [sexEnable, setSexEnable] = React.useState(true);
  const [experienceEnable, setExperienceEnable] = React.useState(true);
  const [maritalEnable, setMaritalEnable] = React.useState(true);
  const [phoneEnable, setPhoneEnable] = React.useState(true);
  const [ageEnable, setAgeEnable] = React.useState(true);
  const [updateButton, setUpdateButton] = React.useState(true);
  const [openNav, setOpenNav] = React.useState(true);
  const loading = useIsMutating();
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  useEffect(() => {
    setFormData(user);
    //eslint-disable-next-line
  }, []);

  const { mutate, isSuccess, reset } = useUpdateUser();

  if (isSuccess) {
    reset();
  }
  // enable sex button
  const enableSexEdit = () => {
    setSexEnable(false);
    setUpdateButton(false);
  };

  // enable experience button
  const enableExperienceEdit = () => {
    setExperienceEnable(false);
    setUpdateButton(false);
  };

  // enable marital button
  const enableMaritalEdit = () => {
    setMaritalEnable(false);
    setUpdateButton(false);
  };

  // enable phone button
  const enablePhoneEdit = () => {
    setPhoneEnable(false);
    setUpdateButton(false);
  };

  // enable age button
  const enableAgeEdit = () => {
    setAgeEnable(false);
    setUpdateButton(false);
  };

  // update button
  const updateHandle = () => {
    setUpdateButton(true);
    setAgeEnable(true);
    setMaritalEnable(true);
    setPhoneEnable(true);
    setExperienceEnable(true);
    setSexEnable(true);
    mutate(formData);
  };

  return (
    <div
      className={
        openNav ? styles.mtn__navigation : styles.mtn__navigation_phone
      }
    >
      {/* logo */}

      <div className={openNav ? styles.mtn__logo : styles.mtn__logo_phone}>
        <img src={logo} alt="MTN Logo" />
      </div>
      {/* image */}
      <div
        className={
          openNav ? styles.mtn__oval_header : styles.mtn__oval_header_phone
        }
      >
        <div className={styles.image_container}>
          <img src={user.Passport} alt="MTN Logo" />
        </div>
      </div>
      <div className={openNav ? styles.mtn__url : styles.mtn__url_phone}>
        <ul>
          <li>
            <div>
              <Input
                title={"Email"}
                type={"text"}
                onChange={handleChange}
                value={formData["Email"]}
                name="Email"
              />
            </div>
          </li>
          <li>
            <div>
              <Input
                title={"Sex"}
                type={"text"}
                onChange={handleChange}
                value={formData["Sex"]}
                disabled={sexEnable}
                name="Sex"
              />
            </div>
            <div onClick={enableSexEdit}>
              <FiEdit />
            </div>
          </li>
          <li>
            <div>
              <Input
                title={"Marital status"}
                type={"text"}
                onChange={handleChange}
                value={formData["MaritalStatus"]}
                disabled={maritalEnable}
                name="MaritalStatus"
              />
            </div>
            <div onClick={enableMaritalEdit}>
              <FiEdit />
            </div>
          </li>
          <li>
            <div>
              <Input
                title={"Date of Birth"}
                type={"text"}
                onChange={handleChange}
                value={formData["DateOfBirth"]}
                disabled={ageEnable}
                name="DateOfBirth"
              />
            </div>
            <div onClick={enableAgeEdit}>
              <FiEdit />
            </div>
          </li>
          <li>
            <div>
              <Input
                title={"Years of Experience"}
                type={"text"}
                onChange={handleChange}
                value={formData["YearsOfExperience"]}
                disabled={experienceEnable}
                name="YearsOfExperience"
              />
            </div>
            <div onClick={enableExperienceEdit}>
              <FiEdit />
            </div>
          </li>
          <li>
            <div>
              <Input
                title={"Phone"}
                type={"text"}
                onChange={handleChange}
                value={formData["PhoneNumber"]}
                disabled={phoneEnable}
                name="PhoneNumber"
              />
            </div>
            <div onClick={enablePhoneEdit}>
              <FiEdit />
            </div>
          </li>
        </ul>
        <button
          onClick={updateHandle}
          disabled={updateButton || loading}
          className={openNav ? styles.mtn__btn : styles.mtn__btn_phone}
        >
          {loading ? "...Updating" : "Update"}
        </button>
      </div>
      <div
        className={openNav ? styles.profile__btn : styles.cancel__btn}
        onClick={() => {
          setOpenNav(!openNav);
        }}
      >
        {openNav ? <ImProfile /> : <AiOutlineClose />}
      </div>
    </div>
  );
};

export default Navigation;
