import { useIsMutating } from "@tanstack/react-query";
import { Button, FormGroup, Input } from "mtforms";
import React, { useState } from "react";
import { Modal } from "../../components";
import { errorAlert } from "../../utils";

import Layout from "../shared/layout/layout";
import { useExams, useStartExam } from "./hooks";

const Dashboard = () => {
  const exam = useExams();

  const [open, setOpen] = useState(false);
  const [examId, setExamId] = useState(undefined);
  const [formData, setFormData] = useState({});
  const [errors, setErrors] = useState({});
  const loading = useIsMutating();

  const handleChange = (name, value) => {
    setFormData({ ...formData, [name]: value });
  };

  const validationHandler = (name, error) => {
    setErrors({
      ...errors,
      [name]: error,
    });
  };
  const { mutate, isError, reset, error } = useStartExam();
  const submitHandler = () => {
    const data = { id: examId, ...formData };
    mutate(data);
  };

  const openHandler = (id) => {
    setOpen(!open);
    setExamId(id);
  };
  if (isError) {
    reset();
    errorAlert(error);
  }

  return (
    <Layout>
      <div className="instructions">
        <h3>Examination Instructions</h3>
        <p>
          This examination will test your knowledge in various works of life.
          This examination will test your knowledge in various works of life
        </p>
        <ul>
          <li>There 3 sections of the examination</li>
          <li>You have 2hours as the alloted time</li>
          <li>You are advised to use a minute on a question</li>
        </ul>
      </div>

      {/* table */}
      <div class="tbl-header">
        <table cellPadding="0" cellSpacing="0" border="0">
          <thead>
            <tr>
              <th>S/N</th>
              <th>Batch</th>
              <th>Exam Schedule</th>
              <th>Start Time</th>
              <th>Start Time Deadline</th>
              <th>Status</th>
              <th>Actions</th>
            </tr>
          </thead>
        </table>
      </div>
      <div class="tbl-content">
        <table cellPadding="0" cellSpacing="0" border="0">
          <tbody>
            {exam?.map((item, i) => (
              <tr key={i}>
                <td>{i + 1}</td>
                <td>{item.Batch}</td>
                <td>{item.ExamSchedule}</td>
                <td>{item?.StartTime}</td>
                <td>{item?.StartTime}</td>
                <td>{item.Status}</td>
                <td>
                  <button
                    className="btn btnYellow"
                    onClick={() => openHandler(item?.ExamScheduleIdId)}
                  >
                    Start Exam
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      {/* end of table */}

      {/* Modal */}
      <Modal
        isVisible={open}
        onClose={() => setOpen(!open)}
        content={
          <>
            <div className="modalTitle">Enter Exam Passcode</div>
            {/* <p>{error}</p> */}
            <FormGroup
              onSubmit={submitHandler}
              validation={formData}
              errors={errors}
              setErrors={setErrors}
            >
              <Input
                name="Passcode"
                label="Passcode"
                type="password"
                value={formData["Passcode"]}
                onChange={handleChange}
                size="large"
                required={true}
                validationHandler={validationHandler}
                error={errors.Passcode}
                className="input"
              />

              <Button
                type="submit"
                title="Start Exam"
                loading={loading === 1}
                className="btnYellow"
              />
            </FormGroup>
          </>
        }
      />
      {/* end of Modal */}
    </Layout>
  );
};

export default Dashboard;
