import { useIsMutating } from "@tanstack/react-query";
import React, { useContext, useEffect, useMemo, useState } from "react";
import { useParams } from "react-router-dom";
import swal from "sweetalert";

import Layout from "../shared/layout/layout";
import {
  useAnsweredQuest,
  useAnswerQuest,
  useExamSection,
  useSectionQuest,
} from "./hooks";
import { Circles } from "react-loader-spinner";
import { AuthContext } from "../../context";

const ExamScreen = () => {
  const { id } = useParams();
  const isLoading = useIsMutating();
  const data = useExamSection(id);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [answer, setAnswer] = useState("");
  const [mark, setMark] = useState("");
  const [sectionId, setSectionId] = useState();
  const {
    data: answered = [],
    refetch,
    isLoading: gettingAnsweredQuestions,
  } = useAnsweredQuest(id);
  const { data: questions, isLoading: fetching } = useSectionQuest(sectionId);
  const { mutate, isSuccess, reset } = useAnswerQuest();
  const [questionId, setQuestionId] = useState(null);
  const [selectedOption, setSelectedOption] = useState(null);
  const { user } = useContext(AuthContext);

  useMemo(() => {
    if (sectionId) return;
    setSectionId(data?.data?.[0]?.ExamSectionIdId);
    //eslint-disable-next-line
  }, [data?.isLoading]);

  //   handle candidate response
  const handleAnswer = (item) => {
    setAnswer(item.choice);
    setMark(item.mark);
  };

  //   submit answer
  const nextHandler = (e) => {
    e.preventDefault();
    const formData = {
      id: questions?.[currentIndex]?.QuestionID,
      questionId: questions?.[currentIndex]?.QuestionID,
      Score: mark,
      SelectedChoice: questionOption(answer),
      SectionTitle: questions?.[currentIndex]?.Category,
      ExamScheduleIdId: id,
      examId: id,
      candidateId: user.ID,
      submitId: selectedOption?.SubmitId,
    };
    mutate(formData);
    e.target.reset();
    setSelectedOption(null);
  };
  //   get new question on submit
  if (isSuccess) {
    refetch();
    reset();
    setCurrentIndex(currentIndex + 1);
  }

  //   go to previous question
  const prevHandler = () => {
    setCurrentIndex(currentIndex - 1);
  };

  useEffect(() => {
    // when the user loses focus
    window.addEventListener("blur", () => {
      swal({
        title: "Warning!",
        text: "Your Exam would be submitted in 5Secs",
        icon: "warning",
        buttons: true,
        dangerMode: true,
      }).then((willDelete) => {
        if (willDelete) {
          //    dispatch(deleteQCat(id));
        }
      });
    });

    // when the user's focus is back to your tab (website) again
    return window.addEventListener("focus", () => {
      console.log("Okay");
    });
  }, []);

  const checkIfAnswered = (id) => {
    var isAnswered = false;
    for (var i = 0; i < answered.length; i++) {
      if (answered[i].QuestionId === id) {
        isAnswered = true;
        break;
      }
    }
    return isAnswered;
  };

  useEffect(() => {
    if (fetching) return;
    setQuestionId(questions?.[currentIndex]?.QuestionID);
    //eslint-disable-next-line
  }, [fetching, currentIndex]);
  useEffect(() => {
    if (gettingAnsweredQuestions) return;
    const res = answered?.filter((it) => it.QuestionId === questionId).length
      ? answered?.filter((it) => it.QuestionId === questionId)[0]
      : null;
    setSelectedOption(res);
    handleAnswer({
      score: res?.mark,
      title: res?.choice,
    });
  }, [questionId, gettingAnsweredQuestions, answered]);

  const questionOption = (option) => {
    switch (option) {
      case true:
        return "True";
      case false:
        return "False";
      default:
        <></>;
    }
    return option;
  };

  return (
    <Layout>
      {/* Sections */}
      <div className="btnContainer">
        {data.data.map((item, i) => (
          <button
            className={`menuBtn ${
              sectionId === item.ExamSectionIdId ? "activeMenu" : ""
            }`}
            onClick={() => setSectionId(item.ExamSectionIdId)}
            key={item.ExamSectionIdId}
          >
            {item.ExamSection}
          </button>
        ))}
      </div>
      {/* end of Sections */}

      {(() => {
        if (data.isLoading) return <Circles />;
        return (
          <div className="questions">
            <h4>{questions?.[currentIndex]?.Category}</h4>
            <p>{questions?.[currentIndex]?.Question}</p>
            <form onSubmit={nextHandler}>
              <ul>
                {questions?.[currentIndex]?.Answers.map((item, i) => (
                  <p key={i} className="options">
                    <input
                      type="radio"
                      key={i}
                      value={item?.title || ""}
                      onChange={(e) => {
                        setSelectedOption({
                          ...selectedOption,
                          SelectedChoice: e.target.value,
                        });
                        handleAnswer(item);
                      }}
                      name="answer"
                      checked={
                        // eslint-disable-next-line eqeqeq
                        selectedOption?.SelectedChoice == item?.choice
                          ? true
                          : null
                      }
                    />
                    &nbsp;&nbsp;{questionOption(item?.choice)}
                  </p>
                ))}
              </ul>
              <div className="btnContainer">
                <button
                  className="btn btn__white marginRight"
                  type="button"
                  onClick={prevHandler}
                  disabled={currentIndex === 0}
                >
                  Previous
                </button>
                <button
                  className="btn btnYellow marginRight"
                  type="submit"
                  disabled={isLoading === 1}
                >
                  Next
                  {isLoading === 1 && <span className="loading"></span>}
                </button>
              </div>
            </form>

            {/* pagination */}
            <div className="btnContainer">
              {questions?.map((item, i) => (
                <button
                  className={`btnSMtable   ${
                    currentIndex === i ? "btnRed" : ""
                  } ${checkIfAnswered(item.QuestionID) ? "btnYellow" : ""}`}
                  onClick={() => setCurrentIndex(i)}
                >
                  {i + 1}
                </button>
              ))}
            </div>
            {/* end of pagination */}
          </div>
        );
      })()}
    </Layout>
  );
};

export default ExamScreen;
