import { axiosInstance } from "../../../axiosInstance";
import { useMutation, useQuery } from "@tanstack/react-query";
import { queryKeys } from "../../../react-query/constants";

import { errorAlert, successAlert } from "../../../utils";
import { getStoredUser } from "../../../storage";
import { useNavigate } from "react-router-dom";

const SERVER_ERROR = "There was an error contacting the server.";

const getMyExams = async () => {
  const data = await axiosInstance({
    url: "/exam",
    method: "GET",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${getStoredUser()}`,
    },
  });
  return data?.data?.data;
};

const startExam = async (formData) => {
  const data = await axiosInstance({
    url: `/exam/${formData["id"]}/passcode-verify`,
    method: "POST",
    data: formData,
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${getStoredUser()}`,
    },
  });

  return data?.data?.data;
};

export function useExams() {
  const fallback = [];
  const { data = fallback } = useQuery({
    queryKey: [queryKeys.exam],
    queryFn: () => getMyExams(),

    onError: (error) => {
      const err = error?.response?.data?.error
        ? error?.response?.data?.error
        : SERVER_ERROR;

      errorAlert(err);
    },
  });
  return data;
}

export function useStartExam() {
  const navigate = useNavigate();
  const { mutate, isSuccess, reset, isError, error } = useMutation({
    mutationFn: (formData) => startExam(formData),
    onSuccess: (data) => {
      successAlert("Success");
      navigate(`/exam/${data}`);
    },
  });
  return { mutate, isSuccess, reset, isError, error };
}
