import React from "react";
import "./index.css";

const Input = ({ onChange, value, type, title, disabled = true, name }) => {
  return (
    <div className="container">
      <label>{title}</label>
      <input
        type={type}
        onChange={onChange}
        value={value}
        disabled={disabled}
        name={name}
      />
    </div>
  );
};

export default Input;
